<template>
  <li class="songs" @click="$emit('song-id', item)">
    <div class="num">
      <slot></slot>
    </div>
    <div class="left">
      <div class="songName">{{ item.name }}</div>
      <div class="info">
        <em v-if="item.fee === 8"></em>
        <i v-for="(artist,index) in item.artists" :key="index" class="artist">
          {{ artist.name }}
        </i>
        <b>{{ item.album.name }}</b>
      </div>
    </div>
    <div class="icons">
      <div
        class="play"
        :class="{ current: currentId === item.id, playing: playing }"
      >
        <i></i>
        <i></i>
        <i></i>
        <i></i>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    currentId: {
      type: Number,
    },
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
.songs {
  list-style: none;
  padding: 10px 0;
  display: flex;
  align-items: center;
  //   margin: 0 10px;
  margin-left: 10px;
  border-bottom: 1px solid rgba(221, 208, 208, 0.336);
  line-height: 1.2;
  .num {
    // color: #999;
    // text-align: center;
    // font-size: 17px;

    span {
      // display: block;
      padding-right: 10px;
    }
  }
  .left {
    flex: 1;
    // line-height: 1.5;
    .songName {
      font-size: 17px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .info {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      em {
        display: inline-block;
        width: 18px;
        height: 22px;
        // background: red;
        background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
        background-size: 166px auto;
        background-position: 2px 14px;
      }
      // line-height: 1.8;
      i.artist {
        font-style: normal;
        font-size: 12px;
        color: #888;
        &:after {
          content: "/";
          margin: 0 1px;
        }
        &:last-of-type {
          // color: red;
          &:after {
            content: "-";
            //   margin: 0 3px;
          }
        }
      }
      b {
        font-size: 12px;
        font-weight: normal;
        color: #888;
      }
    }
  }
  .icons {
    .play {
      width: 22px;
      height: 22px;
      margin-right: 10px;
      //   background: red;
      background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
      background-size: 166px auto;
      background-position: -24px 0;
    }
    .current {
      display: flex;
      justify-content: space-evenly;
      background: none;
      width: 22px;
      height: 22px;
      //   background: red;
      i {
        display: inline-block;
        // border-top: 23px solid black;
        width: 3px;
        height: 22px;
        background: red;
        transform-origin: bottom;
        animation: playing 0.5s linear 0.5s infinite;
        //暂停动画
        animation-play-state: paused;
        &:nth-child(1) {
          animation-delay: -0.5s;
        }
        &:nth-child(2) {
          animation-delay: -0.3s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.2s;
        }
      }
      //   当有这个类名时开启动画
      &.playing {
        i {
          // 开始播放动画
          animation-play-state: running;
        }
      }
    }
  }
}
@keyframes playing {
  from {
    transform: scaleY(0.2);
  }
  to {
    transform: scaleY(1);
  }
}
</style>
