<template>
  <div id="app">
    <!-- <div v-if="$route.meta.showNavbar" class="app-nav"> -->
    <div class="top" v-if="$route.meta.showNavbar"><p>Cloud Cat Music</p></div>
    <ul id="nav" v-if="$route.meta.showNavbar">
      <li>
        <router-link to="/">推荐音乐</router-link>
      </li>
      <li>
        <router-link to="/hot">热歌榜</router-link>
      </li>
      <li>
        <router-link to="/search">搜索</router-link>
      </li>
    </ul>
    <!-- </div> -->
    <section class="routes">
      <transition
        name="custom-classes-transition"
        mode="out-in"
        enter-active-class="animate__animated animate__fadeOutLeft"
      >
        <router-view
          @song-id="songId"
          @song-play-list="songPlayList"
          :currentId="currentId ? currentId.id : null"
          :playing="playing"
          :playbarshow="playbarshow"
        />
      </transition>
    </section>

    <!-- 将currentId传给子组件SongList -->
    <audio
      ref="audio"
      :src="currentsongSrc"
      controls
      autoplay
      @playing="playing = true"
      @pause="playing = false"
      @timeupdate="timeupdate"
      @durationchange="durationchange"
    ></audio>
    <Play
      v-if="currentId"
      :currentId="currentId"
      :songplayList="songplayList"
      :playing="playing"
      :currentTime="currentTime"
      :duration="duration"
      @toggle-playing-start="togglePlayingStart"
      @change-progress-value="$refs.audio.currentTime = $event"
      @song-id="songId"
      @prve-song="prveSong"
      @next-song="nextSong"
    ></Play>
  </div>
</template>

<script>
import Play from "./components/Play.vue";
export default {
  components: {
    Play,
  },
  data() {
    return {
      playbarshow: false,
      currentId: null,
      playing: false,
      currentTime: 0,
      duration: 0,
      songplayList: [],
    };
  },
  methods: {
    songId(song) {
      // console.log(song.id);
      // console.log(song);
      this.currentId = song;
      this.playbarshow = true;
      // console.log(this.currentId.id);
    },
    songPlayList(newsongs) {
      this.songplayList = newsongs;
    },
    togglePlayingStart() {
      // 判断播放状态
      //通过$refs获取要操作的dom节点
      if (this.playing) {
        // 暂停播放
        this.$refs.audio.pause();
      } else {
        // 开始播放
        this.$refs.audio.play();
      }
    },
    timeupdate(event) {
      // 当前进度时间
      this.currentTime = event.target.currentTime;
    },
    durationchange(event) {
      //总时长
      this.duration = event.target.duration;
    },
    prveSong() {
      var index = this.songplayList.findIndex((item) => {
        return item.id === this.currentId.id;
      });
      index--;
      // console.log(index);
      index = index < 0 ? this.songplayList.length - 1 : index;
      this.songId(this.songplayList[index]);
    },
    nextSong() {
      var index = this.songplayList.findIndex((item) => {
        return item.id === this.currentId.id;
      });
      index++;
      index = index >= this.songplayList.length - 1 ? 0 : index;
      this.songId(this.songplayList[index]);
    },
  },
  computed: {
    currentsongSrc() {
      if (this.currentId) {
        return `https://music.163.com/song/media/outer/url?id=${this.currentId.id}.mp3`;
      } else {
        return null;
      }
    },
  },
  watch: {
    currentTime(n) {
      if (n === this.duration) {
        this.nextSong();
      }
    },
  },
};
</script>

