<template>
  <div class="home" :class="{ 'active-botton': playbarshow }">
    <HomeTitle>编辑推荐</HomeTitle>

    <ul class="home-cardlist">
      <HomeCard v-for="item in recommend" :key="item.id" :item="item">
      </HomeCard>
    </ul>

    <HomeTitle>最新音乐</HomeTitle>

    <ul class="song-ul">
      <SongList
        v-for="item in newsSong"
        :key="item.id"
        :item="item"
        :currentId="currentId"
        :playing="playing"
        @song-id="$emit('song-id', $event), $emit('song-play-list', newsSong)"
      />
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import HomeTitle from "../components/HomeTitle.vue";
import HomeCard from "../components/HomeCard.vue";
import SongList from "../components/SongList.vue";

export default {
  props: {
    currentId: Number,
    playing: Boolean,
    playbarshow: Boolean,
  },
  name: "Home",
  components: {
    HomeTitle,
    HomeCard,
    SongList,
  },
  data() {
    return {
      recommend: [],
      newsSong: [],
    };
  },
  created() {
    //ajax请求数据
    // 获取推荐歌单
    this.axios
      .get("http://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        // console.log(res.data.result);
        this.recommend = res.data.result;
      });
    //获取推荐新音乐
    this.axios
      .get("http://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        // console.log(res);
        this.newsSong = res.data.result;
        // console.log(this.newsSong);
      });
  },
};
</script>

