<template>
  <div
    class="search"
    @scroll="scroll"
    :class="{ 'active-botton': playbarshow }"
  >
    <!-- 搜索栏 -->
    <form action="" class="f-search" @keyup.enter="value && (searching = true)">
      <div class="input-box">
        <i class="u-svg"></i>
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model.trim="value"
          class="in-text"
          @focus="inputing = true"
          @blur="inputing = false"
        />
        <figure
          class="close"
          v-show="value"
          @click="(value = ''), (searching = false)"
        >
          <i class="u-close"></i>
        </figure>
      </div>
    </form>
    <!-- 热门搜索 -->
    <div class="hot-search" v-show="!value && !searching">
      <p class="hot-search-title">热门搜索</p>
      <ul class="hot-list">
        <SearchLabel
          v-for="(hot, index) in hots"
          :key="index"
          @click="(searching = true), getResult($event)"
        >
          {{ hot.first }}
        </SearchLabel>
      </ul>
      <!-- 缓存列表 -->
      <p class="hot-history" v-if="history != ''">历史记录</p>
      <ol class="local-list">
        <li v-for="(h, index) in history" :key="h">
          <div
            @click="
              searching = true;
              value = h;
            "
          >
            <i
              ><svg
                t="1630128169003"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2304"
                width="16"
                height="16"
              >
                <path
                  d="M511.914 63.99c-247.012 0-447.925 200.912-447.925 447.924s200.913 447.925 447.925 447.925 447.925-200.913 447.925-447.925S758.926 63.989 511.914 63.989z m0 831.687c-211.577 0-383.763-172.186-383.763-383.763 0-211.577 172.014-383.763 383.763-383.763s383.763 172.014 383.763 383.763-172.186 383.763-383.763 383.763z"
                  fill="#cdcdcd"
                  p-id="2305"
                ></path>
                <path
                  d="M672.06 511.914H512.085v-223.79c0-17.718-14.277-32.167-31.995-32.167-17.717 0-31.994 14.45-31.994 32.167V544.08c0 17.717 14.277 31.994 31.994 31.994H672.06c17.718 0 32.167-14.277 32.167-31.994-0.172-17.89-14.621-32.167-32.167-32.167z"
                  fill="#cdcdcd"
                  p-id="2306"
                ></path></svg
            ></i>
            <span>{{ h }}</span>
          </div>
          <span @click="removeItem(index)">x</span>
        </li>
      </ol>
    </div>
    <!-- 搜索建议 -->
    <div class="search-suggest" v-if="value && !searching">
      <h4>搜索"{{ value }}"</h4>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="(searching = true), (value = item.keyword)"
        >
          {{ item.keyword }}
        </li>
      </ul>
    </div>
    <!-- 搜索结果 -->
    <div class="search-result" v-if="searching">
      <h4>最佳搜索</h4>
      <ul>
        <SearchResult
          v-for="(item, index) in searchresult"
          :key="index"
          :item="item"
          :currentId="currentId"
          :playing="playing"
          @song-id="
            $emit('song-id', $event), $emit('song-play-list', searchresult)
          "
        >
        </SearchResult>
      </ul>
      <h4 v-if="!hasMore" class="no-mo">没有更多了</h4>
    </div>
  </div>
</template>
<script>
import SearchLabel from "@/components/SearchLabel.vue";
// import SongList from "../components/SongList.vue";

import SearchResult from "@/components/SearchResult.vue";
export default {
  props: {
    playbarshow: Boolean,
    currentId: Number,
    playing: Boolean,
  },
  components: {
    SearchLabel,
    // SongList,
    SearchResult,
  },
  data() {
    return {
      suggests: [],
      hots: [],
      searchresult: [],
      value: "",
      Page: 0,
      inputing: false,
      searching: false,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
      // history: JSON.parse(window.localStorage.getItem("history")) || [],
    };
  },
  created() {
    //获取热歌搜索列表
    this.axios.get("http://apis.netstart.cn/music/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
  },
  methods: {
    getResult(event) {
      console.log(event.target.innerText);
      this.value = event.target.innerText;
    },
    scroll(event) {
      // console.log(event);
      //判断页面高度加滚轮滚动距离是否等于滚动条高度
      let offsetHeight = event.target.offsetHeight;
      let scrollTop = event.target.scrollTop;
      let scrollHeight = event.target.scrollHeight;
      let oHSl = Math.floor(offsetHeight + scrollTop);
      // 判断hasMore状态，为false没有更多
      if (this.hasMore) {
        if (oHSl === scrollHeight) {
          // console.log("触底了");
          this.getSearch();
        }
      } else {
        console.log("没有更多了");
      }
    },
    getSearch() {
      this.axios
        .get("http://apis.netstart.cn/music/search", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: this.Page * 30,
          },
        })
        .then((res) => {
          this.searchresult.push(...res.data.result.songs);
          this.Page++;
          this.hasMore = res.data.result.hasMore;
        });
      // 本地缓存数据
      //将每次输入的value存入空数组
      this.history = [...new Set([...this.history, this.value])];
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
    removeItem(index) {
      // this.history = this.history.filter((his) => !his[2]);
      // var n = history.indexOf('')
      // window.localStorage.removeItem("history");
      let history = this.history;
      // console.log(history, index);
      history.splice(index, 1);
      window.localStorage.setItem("history", JSON.stringify(history));
    },
  },
  watch: {
    value(n) {
      // 判断是否失去焦点
      if (this.inputing) {
        this.searching = false;
      }
      // 获取搜索建议
      if (n && !this.searching) {
        this.axios
          .get("http://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
        this.Page = 0;
      }
    },
    // 监听searching状态，获取搜索结果并渲染
    searching(n) {
      //判断状态和value是否有值
      if (n && this.value) {
        this.getSearch();
      } else {
        this.searchresult = [];
      }
    },
  },
};
</script>

