<template>
  <div class="bar">
    <section class="playbar">
      <div
        class="foo-left"
        :class="{ playing: playing }"
        @click.stop="$emit('toggle-show-page-list', true)"
      >
        <img
          :src="
            `${
              currentId.song
                ? currentId.picUrl
                : currentId.al
                ? currentId.al.picUrl
                : currentId.album.artist.img1v1Url
            }?imageView=1&type=webp&thumbnail=60x0`
          "
        />
        <span
          >{{ currentId.name }}-{{
            currentId.song
              ? currentId.song.artists[0].name
              : currentId.al
              ? currentId.ar[0].name
              : currentId.artists[0].name
          }}</span
        >
      </div>
      <div class="foo-right">
        <div class="progress" @click="$emit('toggle-playing-start')">
          <canvas width="40" height="40" ref="canvas"></canvas>
          <div class="icon" :class="[playing ? 'pause' : 'play']"></div>
        </div>
        <div @click="$emit('toggle-show-play-list', true)" class="foo-iii">
          <!-- 列表 -->
          <svg
            t="1630129881139"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="1631"
            width="30"
            height="30"
          >
            <path
              d="M128 432c0-35.2 25.104-49.888 55.776-32.624l144.432 81.248c30.672 17.264 30.672 45.504 0 62.752l-144.432 81.248C153.104 641.888 128 627.2 128 592V432zM896 688a32 32 0 0 1-32 32H480a32 32 0 1 1 0-64h384a32 32 0 0 1 32 32zM896 160a32 32 0 0 1-32 32H160a32 32 0 1 1 0-64h704a32 32 0 0 1 32 32zM896 864a32 32 0 0 1-32 32H160a32 32 0 1 1 0-64h704a32 32 0 0 1 32 32zM896 336a32 32 0 0 1-32 32H480a32 32 0 1 1 0-64h384a32 32 0 0 1 32 32zM896 512a32 32 0 0 1-32 32H480a32 32 0 1 1 0-64h384a32 32 0 0 1 32 32z"
              p-id="1632"
              fill="#ffffff"
            ></path>
          </svg>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showPlayList: false,
    };
  },
  props: {
    currentId: Object,
    playing: Boolean,
    currentTime: Number,
    duration: Number,
  },
  mounted() {
    // console.log(this.$refs.canvas);
  },
  computed: {
    percentage() {
      //   计算百分比
      return this.currentTime / this.duration;
    },
  },
  //监听
  watch: {
    //canvas画进度条
    percentage(n) {
      //   console.log(n);
      // 创建画布
      var context = this.$refs.canvas.getContext("2d");
      //清除画布
      context.clearRect(0, 0, 40, 40);
      //创建新路径
      context.beginPath();
      //画圆弧
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "#d43c33";
      //线宽
      context.lineWidth = 2;
      //描边
      context.stroke();

      //创建新路径
      context.beginPath();
      //画圆弧
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (360 * n - 90),
        (Math.PI / 180) * (360 - 90)
      );
      //描边颜色
      context.strokeStyle = "gray";
      //线宽
      context.lineWidth = 2;
      //描边
      context.stroke();
    },
  },
};
</script>

<style lang="less">
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.bar {
  .playbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 55px;
    background: #242424;
    position: fixed;
    bottom: 0;
    left: 0;
    .foo-left {
      width: 70%;
      color: white;
      display: flex;
      align-items: center;
      margin-left: 10px;
      img {
        width: 50px;
        height: 50px;
        margin-top: -30px;
        border-radius: 50%;
        animation: rotate 3s linear infinite;
        animation-play-state: paused;
      }
      span {
        padding-left: 10px;
        font-size: 13px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      &.playing {
        img {
          animation-play-state: running;
        }
      }
    }
    .foo-right {
      display: flex;
      margin-right: 10px;
      .foo-iii {
        width: 25px;
        height: 20px;
        // border: 3px solid white;
        // display: flex;
        // flex-direction: column;
        // align-items: center;
        // flex-wrap: nowrap;
        // justify-content: space-around;
      }
      .progress {
        width: 30px;
        height: 30px;
        margin-right: 10px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        canvas {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          z-index: -1;
        }
        .icon {
          width: 15px;
          height: 15px;
          display: flex;
          justify-content: space-around;
          align-items: center;
          &.play {
            // border: 5px solid darkblue;
            width: 0;
            height: 0;
            border-top: 8px solid transparent;
            border-bottom: 8px solid transparent;
            border-left: 12px solid white;
            transform: translateX(2px);
            //   border-right: 0px solid greenyellow;
          }
          &.pause {
            &::before,
            &::after {
              content: "";
              display: block;
              width: 30%;
              height: 100%;
              background: white;
            }
          }
        }
      }
    }
  }
}
</style>
