<template>
  <div class="playlist" :class="{ 'active-botton': playbarshow }">
    <div class="playlistmsg">
      <div
        class="mask"
        :style="{
          backgroundImage: `url(${detail.coverImgUrl})`,
          backgroundSize: '100%',
        }"
      ></div>
      <div class="user_img">
        <img :src="detail.coverImgUrl" />
        <div>歌单</div>
        <p>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 22 14">
            <path
              fill-rule="evenodd"
              fill="#040000"
              d="M22 16.777c0 1.233-1.121 2.233-2.506 2.233-1.384 0-2.506-1-2.506-2.233v-2.553c0-1.234 1.122-2.233 2.506-2.233.174 0 .343.017.506.046v-1.37h-.033c.017-.22.033-.441.033-.666a8 8 0 0 0-16 0c0 .225.016.446.034.666H4v1.37c.163-.029.333-.046.505-.046 1.384 0 2.506.999 2.506 2.233v2.553c0 1.233-1.122 2.233-2.506 2.233S2 18.011 2 16.777v-2.553c0-.258.059-.501.148-.73A.982.982 0 0 1 2 13.001v-2.667c0-.023.012-.043.013-.067-.004-.088-.013-.176-.013-.266 0-5.523 4.477-10 10-10s10 4.477 10 10c0 .09-.009.178-.014.266.002.024.014.044.014.067v2a.988.988 0 0 1-.36.753c.224.334.36.72.36 1.138v2.552"
              opacity=".15"
            />
            <path
              fill-rule="evenodd"
              fill="#fff"
              d="M20 16.777c0 1.233-1.121 2.233-2.506 2.233-1.384 0-2.506-1-2.506-2.233v-2.553c0-1.234 1.122-2.233 2.506-2.233.174 0 .343.017.506.046v-1.37h-.033c.017-.22.033-.441.033-.666a8 8 0 0 0-16 0c0 .225.016.446.034.666H2v1.37c.163-.029.333-.046.505-.046 1.384 0 2.506.999 2.506 2.233v2.553c0 1.233-1.122 2.233-2.506 2.233S0 18.011 0 16.777v-2.553c0-.258.059-.501.148-.73A.982.982 0 0 1 0 13.001v-2.667c0-.023.012-.043.013-.067-.004-.088-.013-.176-.013-.266 0-5.523 4.477-10 10-10s10 4.477 10 10c0 .09-.009.178-.014.266.002.024.014.044.014.067v2a.988.988 0 0 1-.36.753c.224.334.36.72.36 1.138v2.552"
            /></svg
          >{{ detail.playCount | count }}
        </p>
      </div>
      <div class="msg">
        <p>{{ detail.name }}</p>
        <!-- <p>{{ detail.description }}</p> -->
      </div>
    </div>
    <div class="title">歌曲列表</div>
    <div class="list">
      <ol>
        <SongList
          v-for="(item, index) in detail.tracks"
          :key="item.id"
          :item="item"
          @song-id="
            $emit('song-id', $event), $emit('song-play-list', detail.tracks)
          "
          :currentId="currentId"
          :playing="playing"
          class="ol-list"
          ><span>{{ (index + 1).toString().padStart(2, "0") }}</span>
          <!-- song-id 传给App组件 -->
          <!-- {{ item.name }}-{{ item.ar[0].name }} -->
        </SongList>
      </ol>
    </div>
  </div>
</template>

<script>
import SongList from "../components/SongList.vue";
export default {
  props: {
    playbarshow: Boolean,
    currentId: Number,
    playing: Boolean,
  },
  components: {
    SongList,
  },
  data() {
    return {
      detail: [],
    };
  },
  created() {
    // 在创建前调用getPlayList方法  传入获取到的路由对象的id
    this.getPlayListDetail(this.$route.query.id);
  },
  methods: {
    getPlayListDetail(id) {
      this.axios
        .get("http://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          // console.log(res);
          this.detail = res.data.playlist;
        });
    },
  },
  filters: {
    count: function(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.playlist {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  .playlistmsg {
    position: relative;
    padding: 0 20px;
    width: 100%;
    height: 200px;
    background: rgba(0, 0, 0, 0.25);
    display: flex;
    align-items: center;
    box-sizing: border-box;
    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(0, 0, 0, 0.658), rgba(0, 0, 0, 0.726));
      // filter: blur(50px);
    }
    .mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      filter: blur(15px);
    }
    .user_img {
      width: 145px;
      height: 145px;
      position: relative;
      img {
        width: 100%;
        vertical-align: middle;
      }
      div {
        width: 40px;
        text-align: center;
        font-size: 13px;
        position: absolute;
        top: 10px;
        left: 0;
        color: white;
        border: 1px solid brown;
        border-radius: 0 15px 15px 0;
        background: #af2829;
      }
      p {
        margin: 0;
        padding: 0;
        position: absolute;
        top: 0;
        right: 0;
        color: white;
        font-size: 13px;
        svg {
          width: 12px;
          height: 12px;
        }
      }
    }
    .msg {
      position: relative;
      margin: -100px 0 0 10px;
      color: white;
      font-weight: 400;
    }
  }
  .title {
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }
  .list {
    ol {
      li {
        margin: 0;
        padding: 10px;
      }
    }
  }
}
.active-botton {
  // padding-bottom: 100px;
  // height: 100%;
  height: 90vh;
}
</style>
