<template>
  <footer class="foo-bar">
    <!-- ------------------------------------PlayBar-------------------------------------- -->
    <transition
      name="custom-classes-transition"
      mode="out-in"
      enter-active-class="animate__animated animate__zoomIn"
      leave-active-class="animate__animated animate__zoomOut"
    >
      <PlayBar
        v-show="!showPlayPage"
        :currentId="currentId"
        :playing="playing"
        :currentTime="currentTime"
        :duration="duration"
        @toggle-show-play-list="showPlayList = $event"
        @toggle-playing-start="$emit('toggle-playing-start')"
        @toggle-show-page-list="showPlayPage = $event"
      />
    </transition>

    <!-- ------------------------------------PlayList-------------------------------------- -->
    <transition
      name="custom-classes-transition"
      mode="out-in"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <PlayList
        v-if="showPlayList"
        :songplayList="songplayList"
        :playing="playing"
        :currentId="currentId"
        @toggle-show-play-list="showPlayList = $event"
        @song-id="$emit('song-id', $event)"
      />
    </transition>

    <!-- ------------------------------------PlayPage-------------------------------------- -->
    <transition
      name="custom-classes-transition"
      mode="out-in"
      enter-active-class="animate__animated animate__bounceIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <PlayPage
        v-show="showPlayPage"
        :currentId="currentId"
        :playing="playing"
        :currentTime="currentTime"
        :duration="duration"
        @toggle-show-play-page="showPlayPage = $event"
        @toggle-show-play-list="showPlayList = $event"
        @toggle-playing-start="$emit('toggle-playing-start')"
        @change-progress-value="$emit('change-progress-value', $event)"
        @next-song="$emit('next-song')"
        @prve-song="$emit('prve-song')"
      />
    </transition>
  </footer>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayList from "@/components/PlayList.vue";
import PlayPage from "@/components/PlayPage.vue";
export default {
  components: {
    PlayBar,
    PlayList,
    PlayPage,
  },
  props: {
    currentId: Object,
    songplayList: Array,
    playing: Boolean,
    currentTime: Number,
    duration: Number,
  },
  data() {
    return {
      showPlayList: false,
      showPlayPage: false,
    };
  },
};
</script>

<style lang="less" scoped></style>
